@import url(https://fonts.googleapis.com/css2?family=Rampart+One&display=swap);
.searchInputs {
    margin-top: 105px;
    display: flex;

}

.search input  {
    background-color: white;
    border: 2px solid white;
    border-radius: 5px;
    font-size: 20px;
    padding: 15px;
    height: 50px;
    width: 80%;
    margin-left: 2.5%;
    margin-right: 2%;
}


input:focus {
    outline: none;
    border: 2px solid #1DA1F2;
}


.dropdown {
  height: 50px;
  width: 10%;
  font-size: 18px !important;
  font-weight: 800 !important;
}

.submitButton {
    display: none;
}

.hashtag {
    margin-right: 5%;
}


.nav-brand {
    font-size: 50px;
    font-family: 'Rampart One', cursive;
}

.nav {
    height: 10rem;
    background: rgb(123,67,151);
background: linear-gradient(90deg, rgba(123,67,151,1) 24%, rgba(220,36,48,1) 100%);
}
.App {
    min-height: 100vh;
    background: rgb(123,67,151);
    background: linear-gradient(90deg, rgba(123,67,151,1) 24%, rgba(220,36,48,1) 100%);
}
.card {
    height: 18rem;
}

.results {
    margin: 2%;
}

h1 {
    color: white;
}

.search-heading {
    text-align: center;
}

.loading {
    text-align: center;
}

#BarChart { 
    display: flex; 
    flex-direction: row; /* Arrange the Bars Horizontally using FlexBox  */
    align-items: flex-end; /* Have the bars start at the bottom and go up */
    justify-content: space-between;
    width: 220px;
    margin: auto;

  }
  


#pgraphs {
    display: flex;
    justify-content: center;
}

#pgraphs p {
    padding-left: 10px;
    padding-right: 10px;
    color: white;
    font-weight: 800;
}

.graph{
    text-align: center;
}

.graph h1 {
    color: #FFB319;
    text-align: center;
}
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  
    background-color: #333;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
